@media screen and (max-width: 991.98px) {
	.slider {
		width: 150px !important;
	}
}

@media screen and (max-width: 575.98px) {
	.logo {
		font-size: 15px !important;
	}
	.slider {
		width: 100px !important;
	}
}

@media screen and (max-width: 450px) {
	.logo {
		font-size: 15px !important;
	}
	.slider {
		width: 65px !important;
	}
	.color-level {
		font-size: 15px;
	}
	.select-container {
		width: 100px;
	}
}

.Navbar {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	height: 5vh;
}

.logo {
	margin-right: 15px;
	padding: 0 13px;
	font-size: 22px;
	background-color: #eceff1;
	font-family: Roboto;
	height: 100%;
	display: flex;
	align-items: center;
}

.logo a {
	text-decoration: none;
	color: black;
}

.slider {
	width: 340px;
	margin: 0 10px;
	display: inline-block;
}

.rc-slider-track {
	background-color: transparent;
}

.rc-slider-rail {
	height: 8px;
}

.rc-slider-handle,
.rc-slider-handle:active,
.rc-slider-handle:hover,
.rc-slider-handle:focus {
	background-color: green;
	outline: none;
	border: 2px solid green;
	box-shadow: none;
	width: 13px;
	height: 13px;
	margin-left: -7px;
	margin-top: -2px;
}

.select-container {
	margin-left: auto;
	margin-right: 1rem;
}
